.gpt3__features {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  .gpt3__features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;

    h1 {
      font-weight: 800;
      font-size: 34px;
      line-height: 45px;
    }

    p {
      margin-top: 2rem;
      font-weight: 500;
      font-size: 16px;
      line-height: 30px; // identical to box height, or 188%
      color: var(--color-subtext);
    }
  }
  
  .gpt3__features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }
}

/**
 * ----------------------------------------
 * Responsive
 * ----------------------------------------
 */
@media screen and (max-width: 990px) {
  .gpt3__features {
    flex-direction: column;

    .gpt3__features-heading {
      margin: 0 0 2rem 0;
    }
  }
}

@media screen and (max-width: 550px) {
  .gpt3__features {
    .gpt3__features-heading {
      h1 {
        font-size: 28px;
        line-height: 38px;
      }
    }
  }
}