* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  font-family: var(--font-family);
  font-style: normal;
}

body {
  background: var(--color-bg);
}

a {
  color: unset;
  text-decoration: none;
}

/**
 * ----------------------------------------
 * Section spaces
 * ----------------------------------------
 */
.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}

/**
 * ----------------------------------------
 * Gradient background (circle)
 * ----------------------------------------
 */
.gradient__bg {
  /* ff 3.6+ */
  background: -moz-radial-gradient(circle at 3% 25%, rgba(0, 43, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(circle at 3% 25%, rgba(0, 43, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  /* opera 11.10+ */
  background: -o-radial-gradient(circle at 3% 25%, rgba(0, 43, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  /* ie 10+ */
  background: -ms-radial-gradient(circle at 3% 25%, rgba(0, 43, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  /* global 92%+ browsers support */
  background: radial-gradient(circle at 3% 25%, rgba(0, 43, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/**
 * ----------------------------------------
 * animation scale-up-center
 * ----------------------------------------
 */
/* ----------------------------------------------
 * Generated by Animista on 2022-8-27 22:50:24
 * Licensed under FreeBSD License.
 * See http://animista.net/license for more info. 
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */
.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

/**
 * ----------------------------------------
 * Responsive
 * ----------------------------------------
 */
@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }
}
