.gpt3__blog {
  display: flex;
  flex-direction: column;

  .gpt3__blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;

    h1 {
      font-weight: 800;
      font-size: 62px;
      line-height: 75px; // or 121%
      letter-spacing: -0.04em;
    }
  }

  .gpt3__blog-container {
    display: flex;
    flex-direction: row;

    .gpt3__blog-container_groupA {
      flex: 0.75;
      margin-right: 2rem;
    }

    .gpt3__blog-container_groupB {
      flex: 1;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 2rem;
    }
  }
}

/**
 * ----------------------------------------
 * Responsive
 * ----------------------------------------
 */
@media screen and (max-width: 990px) {
  .gpt3__blog {
    .gpt3__blog-container {
      flex-direction: column-reverse;

      .gpt3__blog-container_groupA {
        margin: 2rem 0;

        .gpt3__blog-container_article {
          width: 48%;

          .gpt3__blog-container_article-image {
            height: 250px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .gpt3__blog {
    .gpt3__blog-heading {
      h1 {
        font-size: 48px;
        line-height: 52px;
      }
    }

    .gpt3__blog-container {
      .gpt3__blog-container_groupA {
        width: 100%;

        .gpt3__blog-container_article {
          width: 100%;
        }
      }

      .gpt3__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .gpt3__blog {
    .gpt3__blog-heading {
      h1 {
        font-size: 32px;
        line-height: 42px;
      }
    }
  }
}